    import {reqProjectList} from '@/network/projectReq'

    export default {
        name: "Project",
        data() {
            return {
                projectList:[]
            }
        },
        created(){
            this.getProjectList()
        },
        methods:{
            getProjectList(){
                reqProjectList().then(res=>{
                    this.projectList = res.data
                })
            },
			onShareAppMessage() {
				return {
					title: this.$store.state.companyMsg.seller_company.name,
					path: this.$Route.path + '?sid=' + this.$store.state.sid,
				}
			}

        },

    }
